import React from 'react';
import { Card } from 'antd';
import { Form, Avatar, Button, Input, Row, Col, message, Select, Image } from 'antd';
import { UserOutlined, UploadOutlined } from '@ant-design/icons';
import { ROW_GUTTER } from 'constants/ThemeConstant';
import Flex from 'components/shared-components/Flex';
import axios from 'axios';

import { campaign_logo, campaign_banner } from 'api';

let { Option } = Select;
const CampaignForm = (props) => {
    let { history, location } = props;
    let data = location.state;
    let [state, setState] = React.useState({
        logo: '',
        banner: '',
        logoPreview: data ? campaign_logo + data.campaign_logo : '',
        bannerPreview: data ? campaign_banner + data.campaign_banner : 'https://dreammarket.org/public/uploads/banner_img_no_available.png',
        currencyList: [],
        locationList: []
    });

    const getCurrencyAndLocation = async () => {
        let res = await axios.post('/locationAndCurrency', { user_id: 1 });
        setState({
            ...state,
            locationList: res.data.location,
            currencyList: res.data.currency
        });
    }

    const openFile = (type) => {
        if (type === 'banner') {
            let btn = document.createElement('input');
            btn.type = 'file';
            btn.onchange = e => {
                let file = e.target.files[0];
                setState({ ...state, banner: file, bannerPreview: URL.createObjectURL(file) })
            }
            btn.click();

        } else {
            let btn = document.createElement('input');
            btn.type = 'file';
            btn.onchange = e => {
                let file = e.target.files[0];
                setState({ ...state, logo: file, logoPreview: URL.createObjectURL(file) })
            }
            btn.click();
        }
    }
    const saveCampaign = async values => {
        let hideMessage = message.loading('Creating Campaign', 0);
        try {
            let formData = new FormData();
            formData.append('email', values.email);
            formData.append('banner', state.banner);
            formData.append('logo', state.logo);
            formData.append('aboutus', values.aboutus);
            formData.append('campaign_for', values.campaign_for);
            formData.append('campaignname', values.campaignname);
            formData.append('currency', values.currency);
            formData.append('location', values.location);
            formData.append('user_id', '1');
            formData.append('website', values.website);
            console.log(formData)
            let res = await axios.post('/createcampaign', formData);
            hideMessage()
            message.success(res.data.message);
            history.goBack();
        } catch (error) {
            hideMessage();
            message.error(error.response.data);
            console.log(error.response.data);
        }

    }

    const updateCampaign = async (values) => {
        let hideMessage = message.loading('Updating Campaign', 0);
        try {
            let formData = new FormData();
            if (state.logo !== '') {
                formData.append('logo', state.logo);
            }
            if (state.banner !== '') {
                formData.append('banner', state.banner);
            }
            formData.append('user_id', '1');
            formData.append('campaign_id', data.campaign_id);
            formData.append('email', values.email);
            formData.append('aboutus', values.aboutus);
            formData.append('campaign_for', values.campaign_for);
            formData.append('campaignname', values.campaignname);
            formData.append('currency', values.currency);
            formData.append('location', values.location);
            formData.append('website', values.website);
            let res = await axios.post('/updatecampaign', formData);
            hideMessage()
            message.success(res.data.message);
        } catch (error) {
            hideMessage();
            message.error(error.response.data);
            console.log(error.response.data);
        }
    }

    const onFinishFailed = errorInfo => {
        console.log('Failed:', errorInfo);
    };

    const onRemoveAvater = () => {
        setState({
            ...state,
            logoPreview: ''
        })
    }

    React.useEffect(() => {
        getCurrencyAndLocation();
    }, []);
    return (
        <Card>
            <div style={styles.titleDiv}>
                <h1 style={styles.title}>
                    {
                        data ?
                            'Edit Campaign'
                            :
                            'Create Campaign'
                    }
                </h1>
            </div>
            <Flex alignItems="center" justifyContent='center' mobileFlex={false} className="text-center text-md-left mt-5">
                <Avatar size={90} icon={<UserOutlined />} src={state.logoPreview} />
                <div className="ml-md-3 mt-md-0 mt-3">
                    <Button type="primary" icon={<UploadOutlined />} onClick={openFile}> Change Logo</Button>
                    <Button className="ml-2" onClick={onRemoveAvater}>Remove</Button>
                </div>
                <Image
                    width={150}
                    src={state.bannerPreview}
                    className="ml-5"
                />
                <div className="ml-md-3 mt-md-0 mt-3">
                    <Button type="primary" icon={<UploadOutlined />} onClick={() => openFile('banner')}>Upload Cover</Button>
                    <Button className="ml-2" onClick={onRemoveAvater}>Remove</Button>
                </div>
            </Flex>
            <div className="mt-4">
                <Form
                    name="basicInformation"
                    layout="vertical"
                    onFinish={data ? updateCampaign : saveCampaign}
                    onFinishFailed={onFinishFailed}
                >
                    <Row style={{ justifyContent: 'center' }}>
                        <Col xs={24} sm={24} md={24} lg={16} >
                            <Row gutter={ROW_GUTTER}>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Campaign Name"
                                        name="campaignname"
                                        initialValue={data && data.campaign_campaignname}
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Please input Campaign Name!',
                                            },
                                        ]}
                                    >
                                        <Input />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Currency"
                                        name="currency"
                                        initialValue={data && data.currency_id}
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Please Select Currency!'
                                            },
                                        ]}
                                    >
                                        <Select>
                                            {state.currencyList.map((item, index) => {
                                                return (
                                                    <Option key={item.currency_id} value={item.currency_id}>
                                                        {item.currency_name}
                                                    </Option>
                                                )
                                            })}
                                        </Select>
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Email"
                                        name="email"
                                        initialValue={data && data.campaign_email}
                                        rules={[{
                                            required: true,
                                            type: 'email',
                                            message: 'Please enter a valid email!'
                                        }]}
                                    >
                                        <Input />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Campaign For"
                                        name="campaign_for"
                                        initialValue={data && data.campaign_campaignfor}
                                        rules={[{
                                            required: true,
                                            message: 'Required!'
                                        }]}
                                    >
                                        <Input />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Website"
                                        name="website"
                                        initialValue={data && data.campaign_website}
                                        rules={[{
                                            required: true,
                                            type: 'url',
                                            message: 'Enter a valid website'
                                        }]}
                                    >
                                        <Input />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Location"
                                        name="location"
                                        initialValue={data && data.location_id}
                                        rules={[{
                                            required: true,
                                            message: 'Select Campaign Location'
                                        }]}
                                    >
                                        <Select >
                                            {
                                                state.locationList.map(item => {
                                                    return (
                                                        <Option key={item.location_id} value={item.location_id}>
                                                            {item.location_name}
                                                        </Option>

                                                    )
                                                })
                                            }
                                        </Select>
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={24}>
                                    <Form.Item
                                        label="About Us"
                                        name="aboutus"
                                        initialValue={data && data.campaign_aboutus}
                                        rules={[{
                                            required: true,
                                            message: 'Required',
                                        }
                                        ]}
                                    >
                                        <Input />
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Button type="primary" htmlType="submit" style={{ float: 'right' }}>
                                Save Change
                            </Button>
                        </Col>
                    </Row>
                </Form>
            </div>
        </Card>
    )
}

const styles = {
    container: {
        shadow: 10
    },
    titleDiv: {
        display: 'flex',
        justifyContent: 'center'
    },
    title: {
    }
}

export default CampaignForm;